.navbar {
  background: linear-gradient(135deg, #4361ee 0%, #3a0ca3 100%);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  padding: 0.8rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
}

.logo {
  font-size: 1.6rem;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}

.logo:hover {
  transform: scale(1.05);
}

.navLinks {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin-left: 1rem;
  padding-left: 1rem;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}

.navLink {
  color: rgba(255, 255, 0.9);
  text-decoration: none;
  padding: 0.7rem 1.4rem;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.navLink.primary {
  background-color: rgba(255, 255, 255, 0.15);
  transition: all 0.3s ease;
}

.navLink.primary:hover {
  background-color: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

.navLink.primary i {
  color: #4cc9f0;
}

.navLink::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #4cc9f0;
  transition: width 0.3s ease;
}

.navLink:hover {
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.navLink:hover::after {
  width: 100%;
}

.navLink.active {
  background-color: rgba(255, 255, 255, 0.2);
  font-weight: 700;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-left: 3px solid #4cc9f0;
}

.points {
  color: #ffffff;
  padding: 0.6rem 1.2rem;
  font-weight: 600;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}

.points:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.nickname {
  color: rgba(255, 255, 0.9);
  font-weight: 500;
  padding: 0.6rem 0.3rem;
  transition: color 0.3s ease;
}

.nickname:hover {
  color: #ffffff;
}

.profileLink {
  color: rgba(255, 255, 0.9);
  text-decoration: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.profileLink:hover {
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
  text-decoration: none;
}

.signOutButton {
  background-color: rgba(244, 67, 54, 0.9);
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
}

.signOutButton:hover {
  background-color: #f44336;
  transform: translateY(-2px);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .navLinks {
  gap: 0.3rem;
  flex-wrap: wrap;
}
  
  .navLink {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
  }
  
  .logo {
    font-size: 1.4rem;
  }
}