.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  margin-top: 30px;
}

.options {
  margin: 15px 0;
  padding: 16px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  transition: border-color 0.2s ease;
}

.options:focus-within {
  border-color: #007bff;
}

.optionLabel {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 12px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.optionLabel:hover {
  background-color: #f8f9fa;
}

.optionLabel input {
  margin-right: 12px;
  width: 18px;
  height: 18px;
}

.questionText {
  margin: 24px 0 16px;
  color: #212529;
  font-size: 18px;
  font-weight: 500;
}

.textInput {
  width: 100%;
  padding: 14px;
  margin: 10px 0;
  border: 1px solid #ced4da;
  border-radius: 8px;
  min-height: 120px;
  font-size: 16px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.textInput:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}