.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.title {
  text-align: center;
  margin-bottom: 30px;
  color: #333;
}

.profileCard {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 15px rgba(0,0,0,0.1);
  padding: 30px;
}

.profileHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.editButton {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.editButton:hover {
  background-color: #0056b3;
}

.profileForm {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.label {
  font-weight: 600;
  color: #444;
  font-size: 14px;
}

.input,
.select {
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
  transition: border-color 0.2s;
}

.input:focus,
.select:focus {
  outline: none;
  border-color: #007bff;
}

.disabledInput {
  padding: 10px 12px;
  border: 1px solid #eee;
  border-radius: 6px;
  background-color: #f9f9f9;
  color: #666;
}

.majorDisplay {
  padding: 10px 12px;
  border: 1px solid #eee;
  border-radius: 6px;
  background-color: #f9f9f9;
  min-height: 42px;
  display: flex;
  align-items: center;
}

.majorSelector {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.majorHint {
  font-size: 13px;
  color: #666;
  margin: 0;
}

.majorTags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.majorTag {
  background-color: #e9f5ff;
  color: #007bff;
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.removeTag {
  cursor: pointer;
  font-weight: bold;
}

.newMajorInput {
  margin-top: 10px;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
}

.addMajorButton {
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.addMajorButton:hover {
  background-color: #0056b3;
}

.addMajorButton:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.saveButton {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-top: 20px;
}

.saveButton:hover {
  background-color: #218838;
}

.loading {
  text-align: center;
  padding: 40px;
  color: #666;
}

.error {
  color: #dc3545;
  background-color: #f8d7da;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
}

.success {
  color: #28a745;
  background-color: #d4edda;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
}